.properties-container {
    display: flex;
    flex-direction: column;
    /* justify-content: space-around; */
    width: 100%;
    margin-top: 30px;
    align-items: center;
}
.gallery {
    min-height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    text-align: center;
    border: 1px solid gray;
    font-weight: bold;
    margin-top: 10px;
    /* padding: 10px; */
}
.gallery:hover {
    border: 1px solid #98001E;
}
.view-btn {
    border-radius: 2px;
    font-size: 14px;
    cursor: pointer;
    padding: 8px 18px;
    margin: 4px 2px;
    color: #fff;
    background-color: #BA0C2F;
}
.property-description {
    padding: 18px 10px;  
}

@media screen and (min-width: 769px) {
    .properties-container {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        margin-left: 10%;
        margin-right: 10%;
        width: 80%;
        margin-top: 30px;
    }
}
